
div.tooltip {
    position: absolute;
    text-align: left;
    background: #FFFFFF;
    color: #080808;
    border: 1px solid #000000;
    border-radius: 0px;
    pointer-events: none;
    font-size: 1.3rem;
    padding: 5px;
}

rect.bar {
    fill: rgb(153, 62, 1);
}
rect.bar1 {
    fill: rgb(121, 48, 0);
}
rect.bar2 {
    fill: rgb(92, 37, 0);
}


div#graph{
    position: absolute;
}

div#scroll {
    scroll-padding: auto;
    display: block;
    position: relative;
    left: 50%;
    top: 100px;
    width: 45px;
    height: 90px;
    margin: 0;
    overflow-y: auto;
    overflow-x: hidden;
    -ms-overflow-style: none;
    scrollbar-width: none;
    border-left: 1px solid black;
    border-right: 1px solid black;
    border-top: none;
    border-bottom: none;
    transform: rotate(-90deg) translateY(-100px);
    transform-origin: right top;
}

div#scroll::-webkit-scrollbar {
  display: none;
}

div#scroll > div {
    box-sizing: border-box;
    
    font-size: 30px;
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    width: 45px;
    height: 45px;
    cursor: pointer;
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
    /* transform-origin: right top; */
}

div.state {
    display:flex;
    justify-content: center;
    align-items: center;
}


#states {
    fill: #aaa;
} 

#state-borders {
    fill:none;
    stroke:red;
    stroke-width: 1.5px;
    stroke-linejoin: round;
    stroke-linecap: round;
    pointer-events: none;
}